@media (max-width: 52em) {
  .inputForm {
    flex-direction: column;
  }
  main ul {
    grid-template-columns: repeat(3, 1fr);
  }
}

/* Under 704 px */
@media (max-width: 44em) {
  html {
    font-size: 56.25%;
  }
}

/* Under 624 px */

@media (max-width: 39em) {
  header {
    padding: 2.4rem;
  }

  header h1 {
    font-size: 5.2rem;
  }

  .inputForm {
    font-size: 2rem;
    padding: 1.2rem;
  }

  main ul {
    gap: 2.4rem;
  }

  aside {
    font-size: 2.8rem;
  }

  footer {
    padding: 0.6rem;
    font-size: 1.2rem;
  }
}

/* under 512px */

@media (max-width: 32em) {
  html {
    font-size: 50%;
  }
  .inputForm label {
    font-size: 3.2rem;
  }

  main ul {
    grid-template-columns: repeat(2, 1fr);
  }

  li {
    padding: 1rem;
    text-decoration: none;
    display: flex;
    justify-content: center;
    gap: 0.8rem;
    align-items: center;
    font-size: 2.4rem;
  }

  input[type="checkbox"] {
    height: 2.8rem;
    width: 2.8rem;
    accent-color: #868e96;
  }

  li button {
    font-size: 2.4rem;
    border: none;
    background-color: transparent;
    cursor: pointer;
  }
}

/* under 480px */

@media (max-width: 30em) {
  main {
    padding: 0;
  }

  main ul {
    grid-template-columns: repeat(1, 1fr);
    gap: 1.6rem;
  }
}

@media (max-width: 28em) {
  header h1 {
    font-size: 4rem;
  }

  .inputForm label {
    font-size: 2.4rem;
  }

  aside {
    font-size: 2.4rem;
  }
}
