* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  overflow: scroll;
}

body {
  font-family: "Rubik", sans-serif;
  line-height: 1;
  font-weight: 400;
}

.root {
  width: 100%;
  height: 100vh;
  display: grid;
  grid-template-rows: auto auto 1fr auto auto;
}

header {
  background-color: #495057;
  color: #f1f3f5;
  padding: 3.2rem;
}

header h1 {
  display: block;
  text-align: center;
  font-size: 6.4rem;
}

main {
  padding: 3.2rem 0rem;
  max-width: 100vw;
  background-color: #dee2e6;
  color: #212529;
}

.inputForm {
  padding: 2.4rem;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1.6rem;
  background-color: #868e96;
  font-size: 2.4rem;
  color: #191b1f;
}

.inputs {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1.6rem;
}

.inputForm button,
.inputForm input,
.inputForm select {
  padding: 0.4rem 0.8rem;
  border-radius: 15px;
  border: 2px solid #343a40;
  color: #191b1f;
}

.inputForm button:focus,
.inputForm input:focus,
.inputForm select:focus {
  outline: none;
  box-shadow: 0 0 0 0.8rem #495057;
}

main ul {
  padding: 1.6rem;
  margin: auto;
  max-width: 130rem;
  text-decoration: none;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 3.2rem;
  align-items: center;
  justify-items: center;
  overflow-y: auto;
}

li {
  padding: 1rem;
  text-decoration: none;
  display: flex;
  justify-content: center;
  gap: 1.6rem;
  align-items: center;
  font-size: 2rem;
}

input[type="checkbox"] {
  height: 2rem;
  width: 2rem;
  accent-color: #868e96;
}

li button {
  font-size: 1.6rem;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

li button:focus,
input[type="checkbox"]:focus {
  outline: none;
  box-shadow: 0 0 0 0.4rem #adb5bd;
}

aside {
  background-color: #dee2e6;
  color: #212529;
  text-align: center;
  font-size: 3.2rem;
  padding: 1.6rem;
}

footer {
  padding: 1.2rem;
  font-size: 2.4rem;
  color: #adb5bd;
  background-color: #495057;
}

.checked {
  text-decoration: line-through;
  color: #777;
}
